.sidebar {
	background-color: #0a5757;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	padding-top: 60px;
	box-shadow: inset 0 0 3px gray;
}

.sidebar a {
	display: block;
}

.sidebar ul {
	margin: 0;
	padding: 0;
}

.sidebar ul li {
	list-style: none;
}

.sidebar ul li a {
	display: block;
	color: #fff;
	padding: 5px 4px;
	box-sizing: border-box;
	margin: 5px 0 1px 1px;
	border-radius: 5px 0 0 5px;
	text-decoration: none;
}

.sidebar ul li a.active {
	background: #138496;
	color: #fff;
}
